.social_navbar{
      width: 16.6rem;
      height: 3.4rem;
      align-items: center;
      justify-content: space-between;
      padding-top: 3rem;
      margin-left: 3.4rem;
      position: relative;
}

.social_navbar:before{
      width: 100%;
      height: .1rem;
      background: var(--theme-100);
      opacity: .6;
}

.social_navbar a{
      width: 3.4rem;
      height: 3.4rem;
      background: var(--theme-100);
      border-radius: 50%;
      position: relative;
}

.social_navbar a *{
      color: var(--theme-300);
}