.relation_modal{
      width: 100%;
      height: 100vh;
      min-height: 92rem;
      position: fixed;
      top: 0;
      left: 0;
      background: rgba(0, 0, 0, .8);
      z-index: 5;
      opacity: 0;
      visibility: hidden;
      transition: .5s ease-in-out;
}

.relation_modal.active{
      opacity: 1;
      visibility: visible;
}

.relation_box{
      width: 62rem;
      height: 44.6rem;
      background: var(--theme-300);
      border-radius: 5rem;
      padding: 2.9rem;
      position: relative;
      transform: translateY(15rem);
      transition: .3s .4s ease-in-out;
}

.relation_modal.active .relation_box{
      transform: translateY(0);
}

.relation_avatar{
      width: 34rem;
      height: 34rem;
      border-radius: 50%;
      padding: 1rem;
      border: solid .2rem var(--theme-300);
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-6rem, -9.5rem);
}

.relation_avatar__img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      overflow: hidden;
      border: solid 2rem var(--theme-200);
}

.relation_info{
      width: 30rem;
      height: 20.4rem;
      background: var(--theme-200);
      border-radius: 3rem;
      margin-left: 26rem;
      margin-top: 2rem;
      padding: 2rem;
      flex-direction: column;
}

.relation_info h1{
      font-family: var(--ft-b-4);
      font-size: 4rem;
      line-height: 4rem;
}

.relation_info strong{
      color: var(--theme-100);
      font-family: var(--ft-b-3);
      font-size: 2.4rem;
}

.relation_info__type{
      align-items: center;
      gap: .5rem;
      margin-top: .5rem;
}

.relation_info__type *{
      font-size: 2.4rem;
      color: var(--theme-100);
}

.relation_info__type span{
      color: var(--theme-300);
      font-size: 1.6rem;
}

.relation_info a.homepage{
      color: var(--theme-300);
      font-weight: 500;
}

.relation_info a.chatme{
      background: var(--theme-100);
      color: var(--theme-300);
      border-radius: 999px;
      margin-top: 1rem;
      padding: .5rem 0;
      font-weight: 500;
      height: 2.8rem;
}

.relation_about{
      position: relative;
      width: 48rem;
      height: 13rem;
      margin-top: 3rem;
      margin-left: calc(100% - 48rem);
      background: var(--theme-200);
      border-radius: 3rem;
      padding: 2rem;
}

.relation_about:before,
.relation_about:after{
      width: .8rem;
      left: -2rem;
      border-radius: 999px;
}

.relation_about:before{
      height: calc(100% - 2.5rem);
      background: var(--theme-200);
      top: 0;
}

.relation_about:after{
      height: .8rem;
      background: var(--theme-100);
      bottom: 0;
}

.relation_about__text{
      width: 100%;
      height: 100%;
      overflow-y: auto;
      scrollbar-width: thin;
      scrollbar-color: var(--theme-100) var(--theme-300);
}

.relation_about__text p{
      white-space: pre-line;
      font-size: 1.4rem;
}

.btn_show{
      position: absolute;
      right: 0;
      bottom: 0;
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      background: var(--theme-200);
      transform: translate(1rem, 1rem);
      transition: .3s ease-in-out;
}

.btn_show *{
      color: var(--theme-300);
      font-size: 2rem;
}

.btn_show:hover{
      transform: translate(1rem, 1rem) rotate(360deg);
      background: var(--theme-100);
}