.online_content{
      height: 4.6rem;
      gap: 1rem;
      align-items: center;
}

.online_content__avatar{
      width: 4.6rem;
      height: 4.6rem;
      position: relative;
      background: var(--theme-200);
      border-radius: 50%;
      padding: .3rem;
}

.online_avatar__img{
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      overflow: hidden;
}

.online_dot{
      width: 1rem;
      height: 1rem;
      position: absolute;
      right: 0;
      bottom: .3rem;
      border-radius: 50%;
      box-shadow: 0 0 0 .2rem var(--theme-200);
}

.online_dot.red{
      background: var(--red);
}

.online_dot.green{
      background: var(--green);
}

.online_content > span{
      text-transform: uppercase;
      font-weight: bold;
      font-size: 1rem;
      background: var(--theme-200);
      padding: .3rem 1rem;
      border-radius: 999px;
}