@import url('https://fonts.googleapis.com/css2?family=Bungee&family=Comic+Neue:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Cookie&family=Denk+One&family=Dongle:wght@400;700&family=Leckerli+One&family=Pacifico&family=Playball&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Rubik+Mono+One&family=Smooch+Sans:wght@100..900&display=swap');

:root{
    /** fonts */
    --ft-b-1: "Bungee", sans-serif;
    --ft-b-2: "Rubik Mono One", monospace;
    --ft-b-3: "Denk One", sans-serif;
    --ft-b-4: "Smooch Sans", sans-serif;
    --ft-b-5: "Dongle", sans-serif;
    --ft-s-1: "Playball", cursive;
    --ft-s-2: "Cookie", cursive;
    --ft-s-3: "Pacifico", cursive;
    --ft-s-4: "Leckerli One", cursive;
    --ft-t-1: "Comic Neue", cursive;
    --ft-t-2: "Poppins", sans-serif;


    /** theme */
    --theme-100: #f0f3ef;
    --theme-200: #b8cdb1;
    --theme-300: #647e5c;
    --v-gradient: linear-gradient(90deg, var(--theme-2), var(--theme-1));
    --h-gradient: linear-gradient(-135deg, var(--theme-2), var(--theme-1));
    --a-gradient: linear-gradient(45deg, transparent 35%, var(--light) 100%);
    
    /** colors */
    --bg-200: #f0f3ef;
    --bg-100: #fff;
    --text-200: #647e5c;
    --text-100: #b8cdb1;
    --border: #d6d6d6;
    --light: #ffffff;
    --green: #74e25b;
    --red: #d90000;

    /*** purple **/
    --theme-100: #e7e1ef;
    --theme-200: #d2c1eb;
    --theme-300: #5e3776;
    --bg-200: #e7e1ef;
    --text-200: #5e3776;

    /** social color*/
    --facebook: #3b5995;
    --twitter: #171717;
    --google: #dd4b39;
    --youtube: #c4302b;
    --vimeo: #1ab7ea;
    --dribbble: #ea4c89;
    --github: #333;
    --instagram: #c13584;
    --pinterest: #e60023;
    --flickr: #0063dc;
    --bitbucket: #205081;
    --linkedin: #0e76a8;
    --whatsapp: #12af0a;
    --tumblr: #35465c;
    --tiktok: #222;
    --discord: #5865f2;
    --twitch: #9146ff;
    
    /** globals */
    font-family: var(--ft-t-1);
    font-size: 62.5%;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: auto;
    min-height: 100vh;
    overflow-x: hidden;
    background: var(--bg-200);
    color: var(--text-200);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body *{
    font-size: 1.6rem;
    font-family: var(--ft-t-2);
}

:before,
:after{
    content: '';
    position: absolute;
}

a, span{
    display: inline-block;
}

a{
    text-decoration: none;
}

img{
    widtH: 100%;
    object-fit: cover;
}

ul, ol{
    list-style: none;
}

h1, h2, h3, h4{
    line-height: 120%;
}

button{
    border: none;
    outline: none;
    cursor: pointer;
}

.flex{
    display: flex;
}

.center{
    justify-content: center;
    align-items: center;
}

.grid{
    display: grid;
}

.col-2{
    grid-template-columns: repeat(2, 1fr);
}

.col-3{
    grid-template-columns: repeat(3, 1fr);
}

.col-4{
    grid-template-columns: repeat(4, 1fr);
}

.container{
    width: 96%;
    max-width: 98rem;
    height: auto;
    position: relative;
    margin: 0 auto;
    padding-top: 9rem;
}

.container:before{
    border-radius: 50%;
}

.container:before{
    width: 56.4rem;
    height: 56.4rem;
    background: var(--light);
    box-shadow: 0 0 0 3rem var(--bg-200) inset;
    border: solid .1rem var(--theme-300);
    top: 0;
    left: 0;
    transform: translate(-50%, -50%);
}

@property --a{
    syntax: '<angle>';
    inherits: false;
    initial-value: 0deg;
}

.effect{
    position: relative;
    width: fit-content;
    height: fit-content;
    overflow: hidden;
}

.effect:before{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: repeating-conic-gradient(from var(--a), var(--primary) 0%, var(--primary) 5%, transparent 5%, transparent 40%, var(--primary) 50%);
    animation: rotating 4s linear infinite;
}

.title-g{
   width: fit-content;
   height: fit-content;
   color: transparent;
   background: var(--v-gradient);
   -webkit-background-clip: text;
   -moz-background-clip: text;
   background-clip: text;
}

.effect:after{
    background: var(--bg-200);
    top: .2rem;
    left: .2rem;
}

@keyframes rotating{
    0%{
        --a: 0deg;
    }
    100%{
        --a: 360deg;
    }
}

.scroll-y{
    width: 100%;
    height: 42.5rem;
    overflow-y: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-100), var(--theme-100);
}

.scroll-x{
    width: calc(100% - 13.6rem);
    overflow-x: auto;
    scrollbar-width: thin;
    scrollbar-color: var(--bg-100), var(--theme-100);
}

.scroll-y::-webkit-scrollbar,
.scroll-x::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

.scroll-y::-webkit-scrollbar-thumb,
.scroll-x::-webkit-scrollbar-thumb{
    background: var(--theme-200);
    border-radius: .8rem;
}

::-webkit-scrollbar{
    width: .8rem;
    background: var(--bg-100);
    border-radius: .8rem;
}

::-webkit-scrollbar-thumb{
    background: var(--theme-200);
    border-radius: .8rem;
}

::placeholder{
    font-size: 1.4rem;
    color: var(--theme-300);
}