.gallery_content{
      width: 62.6rem;
      height: auto;
      position: relative;
      background: var(--theme-300);
      padding: 5rem 2.9rem;
      border-radius: 5rem;
}

.gallery_content__slider{
      width: 57rem;
      height: 30rem;
}

.gallery_padding{
      width: 19rem;
      height: 30rem;
      padding: .5rem;
}

.gallery_card{
      width: 100%;
      height: 100%;
      background: var(--theme-300);
      border: solid .3rem var(--theme-200);
      border-radius: 1rem;
      overflow: hidden;
      position: relative;
}

.gallery_card__img{
      width: 100%;
      height: 100%;
}

.gallery_card__img img{
      transition: .3s ease-in-out;
}

.gallery_padding:hover .gallery_card__img img{
      filter: grayscale(1);
}

.gallery_card button{
      width: 4.6rem;
      height: 4.6rem;
      position: absolute;
      background: var(--theme-200);
      z-index: 2;
      right: 1rem;
      bottom: 1rem;
      border-radius: 50%;
}

.gallery_card button *{
      color: var(--theme-300);
      font-size: 2.4rem;
}

.slick-arrow{
      display: none;
      opacity: 0;
      visibility: hidden;
}

.slick-dots{
      bottom: -4rem;
}

.slick-dots li button:before{
      background: var(--theme-100);
      width: 2rem;
      height: .6rem;
      border-radius: 999px;
      color: transparent;
      opacity: 1;
}

.slick-dots li.slick-active button:before{
      opacity: 1;
      color: transparent;
      background: var(--theme-200);
}