.template{
      width: 100%;
      height: auto;
}

.main_title{
      position: absolute;
      top: 0;
      left: 0;
      width: 20rem;
      
}

.main_title:before{
      content: attr(data-name);
      color: var(--theme-300);
      font-size: 4.5rem;
      font-family: var(--ft-s-4);
      font-weight: bold;
      -webkit-text-stroke: 15px var(--theme-300);
      top: 0;
      left: 0;
      white-space: nowrap;
}

.main_title h1{
      position: absolute;
      color: var(--theme-100);
      font-size: 4rem;
      font-family: var(--ft-s-4);
      top: .75rem;
      left: .25rem;
      white-space: nowrap;
      letter-spacing: .18rem;
      text-shadow: -.5rem -.5rem var(--theme-300);
}

.main_title.user{
      transform: translate(-1.5rem, -5.5rem);
}

.main_title.video{
      transform: translate(6rem, -3.5rem);
}

.main_title.friend{
      transform: translate(10rem, -3.5rem);
}

.main_title.photos{
      transform: translate(14rem, -3.5rem);
}

.main_title.relation{
      transform: translate(28rem, -3.5rem);
}

.separator{
      height: 4rem;
      width: 6rem;
      background: var(--theme-300);
      position: absolute;
      left: 0;
      top: 0;
}

.separator:before,
.separator:after{
      width: 7.7rem;
      height: 4rem;
      border-radius: 999px;
      top: 0;
      left: 0;
      background: var(--theme-100);
}

.separator.relation,
.separator.relation:before,
.separator.relation:after{
      height: 3rem;
      background: var(--theme-300);
}

.separator:before{
      transform: translateX(-4.5rem);
}

.separator:after{
      transform: translateX(4.5rem);
}

.separator.rotate{
      transform: rotate(90deg);
}

.separator.rotate.video{
      transform: rotate(90deg);
      left: -5rem;
      top: 5rem;
}

.separator.friend{
      top: -4rem;
      left: 3rem;
}

.separator.photos{
      top: -4rem;
      left: 6.5rem;
}

.separator.relation{
      top: -3rem;
      left: 20rem;
      background: var(--theme-200);
}