.main_content{
      width: 100%;
      height: auto;
      margin-top: 4rem;
      gap: 5rem;
}

.aside_content{
      width: 30.5rem;
      height: auto;
      flex-shrink: 0;
}

.aside_inner{
      width: 100%;
      height: auto;
      position: relative;
      background: var(--theme-300);
      border-radius: 5rem;
      padding: 2.75rem;
}

.wrapper_search{
      width: 100%;
      height: 4rem;
      margin-top: 3rem;
}

.input_search{
      width: 100%;
      height: 4rem;
      border-radius: 999px;
      padding: 0 1.5rem;
      background: var(--theme-200);
      border: none;
      outline: none;
      color: var(--theme-300);
}

.input_search:focus{
      outline: solid .2rem var(--theme-100);
}

.friends_list{
      width: 100%;
      height: auto;
      margin-top: 2rem;
}

.friend_row{
      width: 100%;
      height: 6.4rem;
      margin-bottom: 1rem;
      gap: 1rem;
      align-items: center;
}

.friend_row__img{
      width: 6.4rem;
      height: 6.4rem;
      border-radius: 50%;
      overflow: hidden;
      border: solid .3rem var(--theme-200);
}

.friend_row__info{
      flex: 1;
      height: 100%;
      position: relative;
      justify-content: center;
      flex-direction: column;
}

.friend_row__info span{
      color: var(--theme-200);
      font-weight: 500;
      font-size: 1.4rem;
}

.friend_row__info p{
      color: var(--theme-100);
      font-size: 1.2rem;
      margin-top: .5rem;
}

.friend_row__info a{
      position: absolute;
      width: 4rem;
      height: 4rem;
      top: calc(50% - 2rem);
      right: 0;
      background: var(--theme-200);
      border-radius: 50%;
      color: var(--theme-300);
}

.section_content{
      flex: 1;
      height: auto;
}

.section_inner{
      flex-direction: column;
      align-items: flex-end;
}

.section_about{
      width: 59.4rem;
      height: auto;
      min-height: 13rem;
      padding: 2.9rem;
      background: var(--theme-300);
      border-radius: 5rem;
      position: relative;
}

.section_about:before,
.section_about:after{
      width: .8rem;
      left: -3.2rem;
      border-radius: 999px;
}

.section_about:before{
      height: calc(100% - 2.5rem);
      background: var(--theme-300);
      top: 0;
}

.section_about:after{
      height: .8rem;
      background: var(--theme-200);
      bottom: 0;
}

.section_about__inner{
      width: 100%;
      height: 100%;
      background: var(--theme-200);
      padding: 2rem;
      border-radius: 3rem;
}

.section_about__inner p{
      white-space: pre-line;
      font-size: 1.4rem;
}

.section_gallery{
      margin-top: 4rem;
      width: 62.6rem;
}