.header_top{
      width: 100%;
      height: auto;
}

.header_top__title{
      width: 100%;
      height: 8rem;
      justify-content: flex-end;
      align-items: center;
      gap: 4rem;
}

.header_top__title_box{
      width: 50.7rem;
}

.header_top__title_box h1{
      font-size: 10rem;
      font-family: var(--ft-b-4);
      font-weight: 100;
      text-transform: uppercase;
} 

.header_top__user{
      width: 100%;
      height: 27.5rem;
      gap: 4rem;
      align-items: flex-end;
      margin-top: 3rem;
}

.user_wrapper{
      width: 62rem;
      height: 27.5rem;
      background: var(--theme-300);
      flex-shrink: 0;
      border-radius: 5rem;
      position: relative;
      padding: 2.35rem;
      padding-left: 32.5rem;
}

.user_wrapper__avatar{
      width: 34.5rem;
      height: 34.5rem;
      position: absolute;
      padding: 2rem;
      border-radius: 50%;
      left: 0;
      top: 0;
      background: var(--theme-100);
      transform: translate(-2.2rem, -10rem);
}

.user_avatar__img{
      width: 30.5rem;
      height: 30.5rem;
      background: var(--theme-300);
      border-radius: 50%;
      overflow: hidden;
}

.user_wrapper__info{
      width: 25rem;
      height: 100%;
      margin-left: 2rem;
      position: relative;
}

.user_info__data{
      white-space: 100%;
      height: 13rem;
      background: var(--theme-200);
      margin-top: 1rem;
      padding: 1.5rem;
      border-radius: 2rem;
}

.user_info__data strong{
      font-family: var(--ft-b-3);
      font-size: 2.6rem;
}

.user_info__data p{
      font-weight: 500;
      font-size: 1.4rem;
}

.user_info__data_location{
      margin-top: 1rem;
      align-items: baseline;
      gap: .5rem;
}

.user_info__data_location *{
      color: var(--theme-100);
      font-size: 2.3rem;
}

.user_info__data_location span{
      font-size: 1.6rem;
      color: var(--theme-300);
      font-weight: 500;
}

.user_info__navbar{
      width: 100%;
      height: 2.6rem;
      margin-top: 1rem;
      gap: 1rem;
      justify-content: flex-end;
}

.user_info__navbar a{
      width: 6rem;
      height: 2.6rem;
      background: var(--theme-200);
      text-transform: uppercase;
      border-radius: 999px;
      color: var(--theme-300);
      font-weight: 500;
      font-size: 1rem;
}

.user_info__social{
      position: relative;
      width: 100%;
      height: 5rem;
}

.user_info__social span{
      position: absolute;
      width: fit-content;
      text-transform: uppercase;
      top: -2.5rem;
      left: -3rem;
      color: var(--theme-200);
      font-weight: 500;
      font-size: 1rem;
}

.user_info__social span:before{
      width: 3.6rem;
      height: 6rem;
      border-radius: 0 1rem 0 0;
      border-top: solid .1rem var(--theme-100);
      border-right: solid .1rem var(--theme-100);
      right: -1rem;
      top: calc(50% - .05rem);
      transform: translateX(100%);
      opacity: .6;
}

.user_info__social span:after{
      width: .6rem;
      height: .6rem;
      background: var(--theme-100);
      border-radius: 50%;
      top: calc(50% - .3rem);
      right: -1rem;
}