.header_top__video{
      position: relative;
      width: 31.5rem;
      height: 26.5rem;
      padding: 2.35rem;
      background: var(--theme-300);
      border-radius: 5rem;
}

.video_box{
      width: 100%;
      height: 14.7rem;
      margin-top: 1.5rem;
      border-radius: 2rem;
      overflow: hidden;
}

.video_box iframe{
      border: none;
      outline: none;
}

.video_title{
      width: 100%;
      height: 4rem;
      background: var(--theme-200);
      border-radius: 999px;
      margin-top: 1.5rem;
      padding: 1rem;
      align-items: center;
}

.video_title p{
      font-weight: 500;
      font-size: 1.2rem;
}