.user_wrapper__relation{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
}

.user_relation__button{
      width: 6rem;
      height: 6rem;
      background: var(--theme-300);
      border-radius: 50%;
      position: absolute;
      top: 50%;
      left: 1rem;
      transform: translate(-50%, 0);
}

.user_relation__button *{
      color: var(--theme-100);
      font-size: 3rem;
}

.user_relation__box{
      width: 12rem;
      height: 16rem;
      padding: 1.5rem;
      flex-direction: column;
      background: var(--theme-300);
      border-radius: 1rem;
      box-shadow: 1rem 1rem 20px rgba(0,0,0,.3);
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(5rem, 0);
      opacity: 0;
      visibility: hidden;
      transition: .3s ease-in-out;
      justify-content: space-between;
}

.user_relation__button:hover ~ .user_relation__box{
      opacity: 1;
      visibility: visible;
      transform: translate(5rem, 8rem);
}

.user_relation__avatar{
      width: 9rem;
      height: 9rem;
      border-radius: .6rem;
      overflow: hidden;
}

.user_relation__name{
      width: 100%;
      height: 2.8rem;
      background: var(--theme-100);
      border-radius: 999px;
      text-transform: uppercase;
      font-weight: bold;
}

.user_relation__name span{
      font-size: 1rem;
}